button.wp-block-navigation__responsive-container-open {
    padding: 10px;
    background-color: var(--wp--preset--color--red);
    border-radius: 5px;
    color: var(--wp--preset--color--white);
    position: absolute;
    top: 0;
    right: 0;
}

@include sm-md {
    .logo-menu {
        flex-direction: column;
        gap:20px;
    }
}

.menu-footer a {
    color: #FFF;
}

.sticky-header{
    z-index: 9999;

    &.sticky {
        background: $green-neon;
        position: fixed;
        top:0;
        padding-top: 40px;
        padding-bottom: 20px;
        box-shadow: 0 4px 2px -2px rgb(0 0 0 / 50%);
    }
}

#it24-stripe-button {
    display: none;
}